import React, { Fragment, useEffect } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { Link } from "react-router-dom";
import CartEmpty from "../img/cart.gif";
import {
  getCartProducts,
  deleteProductFromUserCart,
  buyProduct,
} from "../../actions/user";
import Spinner from "../layout/Spinner";
const Profile = ({
  buyProduct,
  auth,
  user,
  getCartProducts,
  deleteProductFromUserCart,
}) => {
  useEffect(() => {
    getCartProducts();
  }, []);

  let totalPurchase = [];

  return user.loading ? (
    <Spinner />
  ) : (
    <div className="container my-4">
      <div>
        <Link className="text-dark h3 " to="/">
          <i className="fa fa-arrow-left" /> Go back
        </Link>
      </div>
      <h2 className="mt-3">
        {!user.loading && "Welcome " + auth.user.username}
      </h2>

      {user.cart.length == [] ? (
        <div className="container">
          <h5>
            <i className="fa fa-info-circle h1" /> Currently you don't have any
            product in your cart
          </h5>
          <img src={CartEmpty} alt="empty cart" />
        </div>
      ) : (
        <div className="row">
          {!auth.loaading &&
            user.cart.map((p) => (
              <Fragment key={p.name}>
                <div className="col-lg-4 col-md-6 col-sm-12 my-2 ">
                  <img
                    className="img-fluid rounded border"
                    src={p.imageUrl[0]}
                    alt="product"
                    height="auto"
                    width="350"
                  />
                </div>
                <div className="col-lg-8 col-md-6 col-sm-12 my-2 p-2 border rounded">
                  <span className="float-right text-danger">{p.price} </span>
                  <h3>{p.name} </h3>

                  <p>
                    {p.description} <br />
                    {p.category}
                  </p>
                  <p>
                    <span className="">
                      <button
                        onClick={() => {
                          deleteProductFromUserCart(p._id);
                          return <Redirect to="/cart" />;
                        }}
                        className="border border-0 bg-white text-danger"
                      >
                        Remove from Cart
                      </button>
                    </span>
                    <strong className="float-right">
                      {" "}
                      <button
                        onClick={(e) => buyProduct(p._id)}
                        className="border border-0 bg-white text-success"
                      >
                        Buy
                      </button>
                    </strong>
                  </p>
                </div>
              </Fragment>
            ))}
        </div>
      )}
    </div>
  );
};

Profile.propTypes = {
  getCartProducts: PropTypes.func.isRequired,
  buyProduct: PropTypes.func.isRequired,
  deleteProductFromUserCart: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => ({
  user: state.user,
  auth: state.auth,
});
export default connect(
  mapStateToProps,
  { getCartProducts, deleteProductFromUserCart, buyProduct }
)(Profile);
